* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  transition: 0.3s;
}

.page {
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: $text-color;
  scroll-behavior: smooth;

  &:has(.page__menu:target),
  &:has(.page__language:target),
  &:has(.page__faq:target),
  &:has(.page__help:target),
  &:has(.page__order:target),
  &:has(.page__complete:target),
  &:has(.page__pay:target) {
    overflow: hidden;
  }

  &__body {
    min-width: 320px;
  }

  &__menu,
  &__language,
  &__faq,
  &__help,
  &__order,
  &__complete,
  &__pay {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
